html, body {
  background-color: black;
  font-family: monospace;
  color: white;
}

label,
textarea {
  font-size: 2rem;
  letter-spacing: 1px;
  background: black;
  color: white;
}

textarea {
  margin: 1rem;
  padding: 10px;
  max-width: 100%;
  line-height: 1.5;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px #999;
}

label {
  display: block;
  margin-bottom: 10px;
}

.center {
  margin: 1rem;
  padding: 3rem;
}